.hbar-container {
  width: calc(100% - 10rem);
  max-width: 100rem;
  display: flex;
  align-items: center;
  padding: 0 5rem;

  @media (max-width: 685px) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
  }
}

.hbar-logo {

  width: 5rem;
  margin-right: 2rem;
}

.hbar-controls {
  margin-left: auto;
  display: flex;
  padding: 1rem;
  color: #fff;
  font-size: 1.6rem;

  @media (max-width:  685px) {
    margin: auto;
  }
}

.hbar-temp {
 margin-right: 5px;
 cursor: pointer;
 color: rgb(212, 212, 212);
 font-size: 1.6rem;

 :hover {
   color: #fff;
 }
}

.screen {
  color: #fff;
  margin: 0 5px;
  font-size: 1.6rem;
  background: transparent;
}

.selected-screen {
  color: #fff;
  margin: 0 5px;
  font-size: 1.6rem;
  border-bottom: solid 1px #fff;
  background: transparent;
}